import topBar from '../../components/topBar.vue'
export default {
  components: {
    topBar: topBar
  },
  data() {
    return {
      address_id: '', // 传过来的地址id
      isDefault: false, // 是否默认地址
      title: '添加收货地址',
      detail: {
        name: '',
        phone: '',
        region: '',
        detail: '',
        tags_id: ''
      },
      tags: [],                   // 标签列表

      // 城市选择
      cityPickerValueDefault: [0, 0, 1],             
      themeColor: '#007AFF'
    }
  },
  onLoad(option) {
    if (option.id) {
      this.isDefault = option.isDefault === 'true' ? true : false
      this.address_id = option.id
      this.title= '编辑收货地址'
      // 获取地址详情
      this.getAddressDetail(option.id)
    }
    // 获取地址标签列表
    this.getTagsList()
  },
  methods: {
    goBack() {
      if(this.address_id) {
        // 离开页面自动保存
        this.saveAddress()   
      }
    },
    
    // 地址确定回调
    onConfirm(e) {
      this.detail.region = e.label.replace(/-/g, ',')
    },
    // 获取地址详情
    getAddressDetail(id) {
      let that = this,
        params= {
          url: that.$api.addressDetail,
          data: {
            address_id: id
          }
        }
      that.$httpRequest(params).then((res) => {
        if (res.code === 1) {
          res.data.detail.region = res.data.detail.region.city + ',' + res.data.detail.region.province + ',' +
            res.data.detail.region.region
          that.detail = res.data.detail
        } else if (res.code === 0) {
          uni.showToast({
            title: res.msg,
            icon: 'none'
          })
        } else {
          uni.showToast({
            title: '收货地址获取失败',
            icon: 'none'
          })
        }
      })
    },
    // 保存或者添加收货地址
    saveAddress() {
      let that = this,
        detail = that.detail
      detail.address_id = that.address_id
      let params= {
        url: that.address_id? that.$api.editAddress: that.$api.addAddress,
        data: detail,
        method: 'POST'
      }
      that.$httpRequest(params).then((res) => {
        // 编辑地址
        if (that.address_id) {
          if (res.code === 1) {
            uni.showToast({
              title: '修改成功',
              icon: 'none'
            })
            uni.navigateBack({
              delta: 1
            })
          } else if (res.code === 0) {
            uni.showToast({
              title: res.msg,
              icon: 'none'
            })
          } else {
            uni.showToast({
              title: '修改失败，请重试',
              icon: 'none'
            })
          }
        } 
         // 添加地址
          else {
          if (res.code === 1) {
            uni.showToast({
              title: '添加成功',
              icon: 'none'
            })
            uni.navigateBack({
              delta: 1
            })
          } else if (res.code === 0) {
            uni.showToast({
              title: res.msg,
              icon: 'none'
            })
          } else {
            uni.showToast({
              title: '添加失败，请重试',
              icon: 'none'
            })
          }
        }
      })
    },
    // 选择省市区
    bindPickerChange(e) {
      console.log('picker发送选择改变，携带值为', e.target.value)
      let region = e.detail.value.join(',')
      this.detail.region = region
    },
    // 保存地址
    saveAction() {
      let that = this
      uni.showModal({
        content: '确认保存？',
        success(res) {
          if (res.confirm) {
            that.saveAddress()
          } else if (res.cancel) {
            console.log('用户点击取消')
          }
        }
      })
    },

    // 选择默认
    switchChange(e) {
      let that = this
      if (e.target.value) {
        let params= {
          url: that.$api.setDefault,
          data: {
            address_id: that.address_id
          },
          method: 'POST'
        }
        that.$httpRequest(params).then((res) => {
          if (res.code === 1) {
            uni.showToast({
              title: '设置成功',
              icon: 'none'
            })
            that.isDefault = true
          } else if (res.code === 0) {
            uni.showToast({
              title: res.msg,
              icon: 'none'
            })
          } else {
            uni.showToast({
              title: '设置失败，请重试',
              icon: 'none'
            })
          }
        })
      } else {
        that.isDefault = false
      }
    },
    // 删除地址
    delAddress() {
      let that = this,
        params= {
          url: that.$api.delAddress,
          data: {
            address_id: that.address_id
          },
          method: 'POST'
        }
      that.$httpRequest(params).then((res) => {
        if (res.code === 1) {
          uni.showToast({
            title: '删除成功',
            icon: 'none'
          })
          uni.navigateBack({
            delta: 1
          })
        } else if (res.code === 0) {
          uni.showToast({
            title: res.msg,
            icon: 'none'
          })
        } else {
          uni.showToast({
            title: '删除失败，请重试',
            icon: 'none'
          })
        }
      })
    },
    // 获取地址标签列表
    getTagsList() {
      let that= this,
        params= {
          url: that.$api.tagsList
        }
      that.$httpRequest(params).then(res => {
        that.tags= res.data.list
      })
    },
    
    // 选择标签
    selectTag(id) {
      console.log(id)
      let that= this
      that.detail.tags_id= id
    }
  }
}
